<template>
  <div>
    <!--  -->
    <Breadcrumb :breadcrumbItem="breadcrumbItem"></Breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="" clearable v-model="query" @clear="fetch()">
            <el-button slot="append" icon="el-icon-search" @click="fetch()"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click='$router.push("/heroes/create")'>
            <i class="el-icon-plus"></i></el-button>
        </el-col>
      </el-row>
      <el-table :data="heroes" border stripe height="550">
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column prop="_id" label="ID" width="230"></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="title" label=""></el-table-column>
        <el-table-column prop="avatar" label="">
          <template slot-scope="scope">
            <img :src="scope.row.avatar" alt="" style="height:3rem;">
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="" width="180">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="small" circle
              @click="$router.push(`/heroes/edit/${scope.row._id}`)"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="small" circle @click="remove(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
        :page-sizes="[10, 15, 20 , 25,]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
        :total="total" background>
      </el-pagination>
    </el-card>
  </div>
</template>
<script>
  import Breadcrumb from '../components/Breadcrumb'
  export default {
    components: {
      Breadcrumb
    },
    data() {
      return {
        heroes: [],
        breadcrumbItem: ['', ''],
        total: 0,
        query: '',
        pageNum: 1, // 
        pageSize: 10, // 
      }
    },
    methods: {
      // 
      async fetch() {
        const res = await this.$http.get(
          `rest/heroes?pageNum=${this.pageNum}&pageSize=${this.pageSize}&query=${this.query}`);
        this.heroes = res.data.items;
        this.total = res.data.count;
      },
      // 
      async remove(row) {
        this.$confirm(`"${row.name}"`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          await this.$http.delete(`rest/heroes/${row._id}`)

          this.$message({
            type: 'success',
            message: '!'
          });
          this.fetch(); // 
        }).catch(() => {

        });
      },
      // 
      handleCurrentChange(newPage) {
        this.pageNum = newPage
        this.fetch()
      },
      // 
      handleSizeChange(newSize) {
        this.pageSize = newSize
        this.fetch()
      }
    },
    created() {
      this.fetch(); // 
    }
  }
</script>
<style lang='less' scoped>
</style>