<template>
  <div>
    <h1></h1>
    <div>
      <el-button type="primary" size="small" @click='$router.push("/models/create")' style="margin-bottom:.5rem;"><i
          class="el-icon-plus"></i></el-button>
    </div>
    <el-table :data="items" border stripe>
      <el-table-column prop="_id" label="ID" width="230"></el-table-column>
      <el-table-column prop="parent.name" label=""></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="180">
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="small" circle
            @click="$router.push(`/models/edit/${scope.row._id}`)"></el-button>
          <el-button type="danger" icon="el-icon-delete" size="small" circle @click="remove(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        items: []
      }
    },
    methods: {
      // 
      async fetch() {
        const res = await this.$http.get('rest/models');
        this.items = res.data;
      },
      // 
      async remove(row) {
        this.$confirm(`"${row.name}"`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          await this.$http.delete(`rest/models/${row._id}`)
          this.$message({
            type: 'success',
            message: '!'
          });
          this.fetch(); // 
        }).catch(() => {

        });
      }
    },
    created() {
      this.fetch(); // 
    }
  }
</script>
